import React, { Component } from 'react';
import { NavBar, Icon, } from 'antd-mobile';
import Content from '../../views/common/Content';

import '../../assets/css/traffic.css';

export default class TrafficAirScreen extends Component
{
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}>
                    航空
                </NavBar>
                <Content style={{backgroundColor: '#efeff4'}}>
                    <div className="trafficDetailCard">
                        <img src={require('../../assets/images/traffic/plane_1.jpg')} alt='' className="trafficDetailImage"/>
                        
                        <div className="trafficDetailNameRow">
                            <div className="trafficDetailNames">
                                <div className="trafficDetailName">
                                    武汉天河机场
                                </div>
                                <div className="trafficDetailSubName">
                                    WuHan TianHe Airport
                                </div>
                            </div>
                            <div className="trafficDetailNameFlag">
                                4F级机场
                            </div>
                        </div>

                        <div className="trafficDetailLR">
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">地址</div>
                                <div className="trafficDetailLRSubItem">湖北省武汉市黄陂区</div>
                            </div>
                            <div className="trafficDetailSplitLR"/>
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">旅客吞吐量</div>
                                <div className="trafficDetailLRSubItem">1894万人（2015年））</div>
                            </div>
                        </div>

                        <div className="trafficDetailIntro">
                            <div className="trafficDetailIntroLine"/>
                            <div className="trafficDetailIntroText">机场简介</div>
                            <div className="trafficDetailIntroLine"/>
                        </div>

                        <div className="trafficDetailContent">
                            <div className="trafficDetailTitle1">天河机场</div>
                            <div className="trafficDetailLongText">
                                天河机场即武汉天河国际机场距离新县120公里。天河国际机场(IATA：WUH；ICAO：ZHHH）位于湖北省武汉市，是中国民航总局指定的华中地区唯一的综合枢纽机场和最大的飞机检修基地、国家一级民用机场。机场一期工程按国际民航组织制定的4E级标准修建，设施完备，可起降各种大型客机。天河国际机场旅客吞吐量和货邮吞吐量均位居中国前列。作为湖北省最大的门户机场和中部地区唯一能够起降空客A380大型飞机的国家干线机场，天河机场2009年旅客吞吐量突破千万；2015年12月中旬旅客吞吐量达到1502.6万人次，ACI（国际机场协会）旅客满意度测评值为4.45，进入全球同量级机场前十名，率先跨入世界百强。
                            </div>

                            <div className="trafficDetailTitle1">机场简介</div>
                            <div className="trafficDetailLongText">
                            天河国际机场（机场3字代码:WUH，机场4字代码:ZHHH）为4F级机场，是中国民航总局指定的华中地区唯一的综合枢纽机场和最大的飞机检修基地。 天河机场因坐落在武汉市黄陂区天河街境内，故名“武汉天河机场”。改革开放总设计师邓小平亲笔题写了武汉机场名。
                                                作为湖北省的省会机场和国内的重要干线机场，天河国际机场是我国率先通过国际ISO9001质量体系认证的机场之一，也是中部地区唯一能够起降空客A380大型飞机的机场。1995年4月15日正式开航，飞行区为4E级，现有跑道长3400米，宽45米，两侧道肩各15米。天河机场2009年旅客吞吐量突破千万，2011年完成旅客吞吐量1246.2万人次，货邮吞吐量12.28万吨，航班起降11.54万架次；ACI（国际机场协会）旅客满意度测评值为4.45，进入全球同量级机场前十名，率先跨入世界百强。
                                                天河国际机场目前正在大力推进三期扩建。此项扩建以满足2020年旅客吞吐量3500万人次、货邮吞吐量44万吨、飞机起降33.6万架次为目标，将新建长3600米、等级为4F的第二条跑道及平行滑行道、联络道和站坪，同时建设面积为35万平方米的T3航站楼，配套建设进场路、商业交通中心、货运区和其它公用设施等。
                            </div>

                            <div className="trafficDetailTitle1">航站楼、设施及航空公司</div>
                            <div className="trafficDetailLongText">
                                天河国际机场现有两座运行中的航站楼，分别为T2航站楼和国际航站楼。T2航站楼坐落于机场主干道——机场大道北侧，国际航站楼坐落于T2航站楼西北侧。 T2航站楼于2008年4月15日正式投入使用，总建筑面积14.98万平方米，按照年旅客吞吐量1300万人次、飞机起降12.2万架次、货物吞吐量32万吨设计，目前主要用于国内进出港流程。国际航站楼于2010年12月28日正式投入使用，建筑总面积为9429平方米，主要用于机场国际/地区航班的进出港流程。
                            </div>

                            <div className="trafficDetailTitle2">天河国际机场第二航站楼</div>
                            <div className="trafficDetailLongText">
                                天河机场T2航站楼于2007年底竣工，2008年4月15日正式投入使用，总投资33.72亿元，历时3年建设。T2航站楼总建筑面积14.98万平方米，同时配套建设25万平方米的站坪和2万平方米的货库；建设规模按照年旅客吞吐量1300万人次、飞机起降12.2万架次、货物吞吐量32万吨设计。
                            </div>

                            <div className="trafficDetailLongText">
                                T2航站楼共设有42个售补票柜台(东航售补票柜台：17-19号；南航售补票柜台：42-50；国航售补票柜台：10-12；深航售补票柜台：30-31；机场售补票柜台：27-29；民航凯亚售补票柜台：38-39)、72个值机柜台、23条安检通道、20个双通道登机桥和14个远机位登机口，可充分满足运行需要。航站楼设有母婴室、吸烟室、无障碍设施、饮水设施等人性化设施，候机服务更加人性化。此外，T2航站楼还采用了目前世界上最先进的行李自动分拣系统和自动泊位引导系统。
                            </div>

                            <img src={require('../../assets/images/traffic/plane_2.jpg')} alt='' className="trafficDetailImage"/>

                            <div className="trafficDetailTitle2">天河国际机场航站楼</div>
                            <div className="trafficDetailLongText">
                                天河机场国际航站楼于2010年6月开工建设，2010年12月28日投入使用，2012年初进行扩建。国际航站楼为主体单层、局部两层结构，离港和到港流程均在同一层，扩建后建筑总面积为9429平方米，同时配套建设18000平方米的道路、停车场，及楼外供电、供水、弱电、雨水及污水排放等设施。
                            </div>
                            <div className="trafficDetailLongText">
                                目前，在国际航站楼运营的航空公司有：中国东方航空公司、中国南方航空公司、中国国际航空公司、法国航空公司、大韩航空公司、港龙航空公司、中华航空公司、复兴航空公司、胜安航空公司、亚洲航空公司、济州航空公司、韩国易斯达航空公司、泰国东方航空公司、远东航空公司
                            </div>

                            <img src={require('../../assets/images/traffic/plane_3.jpg')} alt='' className="trafficDetailImage"/>

                            <div className="trafficDetailTitle1">航线情况介绍</div>
                            <div className="trafficDetailLongText">
                                一、国内客运通航城市（51航点）
                            </div>
                            <div className="trafficDetailLongText">
                                北京、包头、重庆、成都、长春、大庆、大连、恩施、福州、广州、桂林、贵阳、海口、哈尔滨、呼和浩特、海拉尔、杭州、济南、晋江、揭阳、昆明、库尔勒、兰州、丽江、绵阳、南宁、宁波、南通、青岛、上海、三亚、深圳、沈阳、神农架、天津、太原、台州、乌鲁木齐、温州、无锡、西双版纳、西安、厦门、襄阳、西宁、烟台、盐城、银川、珠海、张家界、遵义。
                            </div>
                            <div className="trafficDetailLongText">
                                二、国内货运通航城市（8个）
                            </div>
                            <div className="trafficDetailLongText">
                                北京、西安、长沙、南京、石家庄、深圳、杭州、郑州。
                            </div>
                            <div className="trafficDetailLongText">
                                三、国际及地区通航城市（49个）
                            </div>
                            <div className="trafficDetailLongText">
                                雅典、阿姆斯特丹、奥克兰、曼谷、巴厘岛、巴塞罗那、波士顿、柏林、布鲁塞尔、芝加哥、杜塞尔多夫、法兰克福、福冈、香港、胡志明市、夏威夷•火奴鲁鲁、汉堡、雅加达、高雄、吉隆坡、伦敦、洛杉矶、澳门、墨尔本、米兰、马德里、曼彻斯特、慕尼黑、马累、莫斯科、纽约、名古屋、大阪、巴黎、普吉岛、釜山、罗马、首尔、悉尼、新加坡、旧金山、西雅图、斯德哥尔摩、台北、东京、多伦多、台中、温哥华、苏黎世。
                            </div>

                            <div className="trafficDetailTitle1">新县至天河机场线路</div>
                            <div className="trafficDetailLongText">
                                从新县站出发做火车至武汉汉口火车站下火车，到汉口金家墩客运站乘坐机场大巴（06:05至20:05，每30分钟一班）到达天河机场。
                            </div>
                        </div>
                    </div>
                </Content>
            </div>
        );
    }
}


